* {margin: 0; padding: 0; box-sizing: border-box;}
header {
  background-color: aliceblue;
  padding: 20px 10px;
  font-weight: bold;
}

.list {
  padding: 20px 10px;
  border-bottom: 1px solid lightgray;
  display: flex;
  flex-wrap: wrap;
}

.listname {
  width: calc(100% - 50px);
  color: deepskyblue;
}

.good {width: 50px;}
.good>span {
  cursor: pointer;
}
.date {
  width: 100%;
  font-size: 0.75rem;
  padding-top: 10px;
}

.modal {
  background-color: #eee;
  padding: 20px;
  position: absolute;
  top: 100px;
  right: 0;
  width: 50%;
}